import React from "react"
import { Carousel } from "react-responsive-carousel"
import "react-responsive-carousel/lib/styles/carousel.min.css"

import bourbon from "../images/galway-bourbon.jpg"
import coffee from "../images/galway-coffee.jpg"
import dragon from "../images/galway-dragon.jpg"
import gin from "../images/galway-gin.jpg"
import mango from "../images/galway-mango.jpg"
import orange from "../images/galway-orange.jpg"
import rye from "../images/galway-rye.jpg"
import vodka from "../images/galway-vodka.jpg"

import socOne from "../images/soc-2.gif"
import socTwo from "../images/soc-3.jpg"
import socThree from "../images/soc-4.jpg"

const ReactCarousel = ({ mode }) => {
  return (
    <div>
      {mode === "centerMode" &&
        <div style={{ paddingTop: "100px", maxWidth: "50%" }} className="container carousel-custom-container">
          <div className="row">
            <div className="col-12">
              <Carousel centerMode infiniteLoop useKeyboardArrows autoPlay showStatus={false}>
                <div>
                  <img src={bourbon} alt="Galway Bourbon Label" />
                </div>
                <div>
                  <img src={coffee} alt="Galway Coffee Label" />
                </div>
                <div>
                  <img src={dragon} alt="Galway Dragon Label" />
                </div>
                <div>
                  <img src={gin} alt="Galway Gin Label" />
                </div>
                <div>
                  <img src={mango} alt="Galway Mango Label" />
                </div>
                <div>
                  <img src={orange} alt="Galway Orange Label" />
                </div>
                <div>
                  <img src={rye} alt="Galway Rye Label" />
                </div>
                <div>
                  <img src={vodka} alt="Galway Vodka Label" />
                </div>
              </Carousel>
            </div>
          </div>
        </div>
      }

      {mode === "normal" &&
        <div className="container-fluid container-carousel-normal" style={{ padding: "0", margin: "0" }}>
          <div className="row" style={{ padding: "0", margin: "0" }}>
            <div className="col-12" style={{ padding: "0" }}>
              <Carousel infiniteLoop useKeyboardArrows autoPlay showStatus={false} showIndicators={false} showThumbs={false} dynamicHeight={true} >
                <div>
                  <img src={socOne} alt="ONT project gif" />
                </div>
                <div>
                  <img src={socTwo} alt="ONT project image" />
                </div>
                <div>
                  <img src={socThree} alt="ONT project image Label" />
                </div>
              </Carousel>
            </div>
          </div>
        </div>
      }
    </div>
  )
}

export default ReactCarousel
